'use client';

import { useEffect, useState } from 'react';

const navLinks = [
  { href: '/', label: 'Naslovna' },
  { href: '/audio', label: 'Аудио' },
  { href: '/video', label: 'Видео' },
  { href: '/propovedi', label: 'Проповеди' },
  { href: '/kontakt', label: 'Контакт' },
];

export default function Header() {
  const [showNav, setShowNav] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  const handleScroll = () => {
    const currentY = window.scrollY;
    setShowNav(currentY < lastScrollY || currentY < 10);
    setLastScrollY(currentY);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [lastScrollY]);

  return (
    <header className="relative h-[90vh] bg-cover bg-center" style={{ backgroundImage: "url('/header.jpg')" }}>
      <nav className={`fixed top-0 left-0 w-full z-50 transition-transform duration-300 ${showNav ? 'translate-y-0' : '-translate-y-full'} bg-gradient-to-r from-[#1c3c3c] to-[#2a2d4e] text-white shadow-md`}>
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">
          <div className="text-xl font-bold">ХБЦ</div>
          <div className="space-x-6">
            {navLinks.map(link => (
              <a key={link.href} href={link.href} className="hover:underline">
                {link.label}
              </a>
            ))}
          </div>
        </div>
      </nav>
      <div className="absolute inset-0 flex items-center justify-center bg-gradient-to-r from-[#168c83b5] to-[#3d3f9dc9]">
        <h1 className="text-4xl md:text-6xl font-bold text-white text-center">
          Свима нам је потребан <span className="font-extrabold">Исус</span>
        </h1>
      </div>
    </header>
  );
}