'use client';

import { useEffect, useState } from 'react';

function getNextSunday10AM() {
  const now = new Date();
  const nextSunday = new Date();
  nextSunday.setDate(now.getDate() + ((7 - now.getDay()) % 7));
  nextSunday.setHours(10, 0, 0, 0);
  if (nextSunday < now) nextSunday.setDate(nextSunday.getDate() + 7);
  return nextSunday;
}

export default function Countdown() {
  const [timeLeft, setTimeLeft] = useState(getNextSunday10AM() - new Date());

  useEffect(() => {
    const interval = setInterval(() => {
      setTimeLeft(getNextSunday10AM() - new Date());
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  const totalSeconds = Math.floor(timeLeft / 1000);
  const days = Math.floor(totalSeconds / (3600 * 24));
  const hours = Math.floor((totalSeconds % (3600 * 24)) / 3600);
  const minutes = Math.floor((totalSeconds % 3600) / 60);
  const seconds = totalSeconds % 60;

  return (
    <section className="bg-[#1c1f3c] text-white py-8 text-center">
      <h2 className="text-xl md:text-2xl font-semibold mb-4">Следеће богослужење је за:</h2>
      <div className="flex justify-center gap-6 text-lg md:text-2xl">
        <div><strong>{days}</strong><div className="text-sm">дана</div></div>
        <div><strong>{hours}</strong><div className="text-sm">сат</div></div>
        <div><strong>{minutes}</strong><div className="text-sm">минута</div></div>
        <div><strong>{seconds}</strong><div className="text-sm">секунди</div></div>
      </div>
    </section>
  );
}
